<template>
  <div>

    <div class="page">

      <h2 style="display: inline"></h2>
      <el-button class="send" type="primary"  @click="publish()"></el-button>
      <el-table :data="postList" stripe style="width: 100%">

        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="text" @click="gotopost(scope.row.post_id)">{{ scope.row.title }}</el-button>

          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <span >{{ scope.row.name }}</span>

          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <span >{{ scope.row.post_time }}</span>
          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <span >{{ scope.row.last_reply_time }}</span>
          </template>
        </el-table-column>

        <el-table-column label="" >
          <template slot-scope="scope">
            <span >{{ scope.row. reply_num }}</span>
          </template>
        </el-table-column>


      </el-table>
      <el-dialog title="" :visible.sync="dialogVisible" width="50%">
        <h3></h3>
        <el-input type="text" placeholder="" v-model="title"  maxlength="30" show-word-limit ></el-input>
        <h3></h3>
        <el-input   type="textarea" :rows="5" placeholder="" v-model="content" maxlength="160" show-word-limit></el-input>
        <span slot="footer" class="dialog-footer">
       <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary" @click="handleEdit"> </el-button>
     </span>
      </el-dialog>

    </div>

    <div class="paging">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page="currentPage"
        @current-change="handleCurrentChange"
        :total=tieNum>
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import {reqPostList,releasePost} from "../../../api";

  export default {
    name :'question',
    data() {
      return {
        currentPage:1, //
        tieNum:null,
        dialogVisible:false,
        title:"",
        content:"",
        postList: [

        ]
      }
    },
    methods: {
      publish()
      {
        this.dialogVisible=true;
      },
      handleCurrentChange: async function(currentPage){
        this.currentPage = currentPage;
        const result=await reqPostList(0,this.currentPage)/*type 10*/
        this.postList=result.tieList;

      },
      async handleEdit(){
        if(this.content.trim()===''||this.title.trim().length<5) {
          this.$message.error(",5")
        }
        else{
          const result=await releasePost(this.$store.state.id,this.title,this.content,0)/*1*/
          if(result.state_code===0){
            this.$message.success("")
            setTimeout(async() =>{
              const result=await reqPostList(0,this.currentPage)
              this.postList=result.tieList;
              this.tieNum=result.tieNum
              this.title=''
              this.content=''
            },100);
          }
          this.dialogVisible=false;
        }

      },
      gotopost(post_id){
        this.$router.push({
          path: '/center/activities/questiondetail',
          query:{
            post_id:post_id
          }
        })
      }
    },
    async mounted() {
      const result=await reqPostList(0,this.currentPage)/*type 10*/
      if(result.state_code===0){
        this.postList=result.tieList;
        this.tieNum=result.tieNum
      }
    }
  }
</script>

<style scoped>
  @import "../../../assets/style/style.css";
</style>



