<template>
  <div class="Header">
  <div id="nav">
    <el-menu router :default-active="$route.path" class="el-menu-demo" mode="horizontal"
             background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
      <el-menu-item v-if="!isManager" index="/center/index"></el-menu-item>
      <el-menu-item v-if="!isManager" index="/center/syllabus"></el-menu-item>
      <el-menu-item v-if="!isManager" index="/center/resources"></el-menu-item>
      <el-menu-item v-if="!isManager" index="/center/activities"></el-menu-item>
      <el-menu-item index="/center/forum"></el-menu-item>
      <el-menu-item index="/center/personInfo"></el-menu-item>

    </el-menu>
    <router-view ></router-view>
  </div>
  </div>
</template>

<script>
    export default {
        name: "Nav",
      data(){
          return{
            isManager:false
          }
      },
      mounted() {
          if(this.$store.state.status===-1){
            this.isManager=true
          }
      }
    }
</script>

<style scoped>
  @import "../../assets/style/Header.css";
</style>



